

import React, { useState, useEffect } from "react";
import MovieList from "./MovieList";
import ResultList from "../../search/ResultList";// kt qu ca input truy vn tm kim 
import Banner from "../Banner";// hin th nh b phim bt k ly t original.
import { token, requests } from "./requests";
import { requestData } from "./requests";
import styles from "./RenderDom.module.css";

// tch ly API ca tng mc phim
function RenderDom() {
  const [searchTerm, setSearchTerm] = useState(""); // ly d liu query a vo Resultlist 
  const [originalMovies, setOriginalMovies] = useState([]);// oirginal
  const [trendingMovies, setTrendingMovies] = useState([]);//xu hng 
  const [topRatedMovies, setTopRatedMovies] = useState([]);//xp hng cao 
  const [actionMovies, setActionMovies] = useState([]);//hnh ng 
  const [comedyMovies, setComedyMovies] = useState([]);//hi kch
  const [horrorMovies, setHorrorMovies] = useState([]);//kinh d
  const [romanceMovies, setRomanceMovies] = useState([]);//lng mn
  const [documentaryMovies, setDocumentaryMovies] = useState([]);//phim ti liu
    
  //Ly API cho original
    const fetchOriginalMovies = async () => {
      try{
        const response = await fetch(requestData + requests.fetchNetflixOriginals, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      //console.log("Original", response)
      const result = await response.json();
      //console.log("result Original", result)
      
        setOriginalMovies(result.results);
      
    } catch (error) {
      console.log("Error fetching data:", error);
    }
  }
    
  //ly API cho xu hng 
    const fetchTrendingMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchTrending, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
     // console.log("response Trending", response)
      const result = await response.json();
      //console.log("result Trending", result)
      setTrendingMovies(result.results);
      
    } catch (error) {
      console.log("Error fetching data:", error);
    }
    };


    //ly API cho top hng cao 
    const fetchTopRatedMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchTopRated, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
     // console.log("Xp hng cao", response)
      const result = await response.json();
     // console.log("result TopRate", result)
      setTopRatedMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};
 

    //Ly API cho phim hnh ng
    const fetchActionMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchActionMovies, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
     // console.log("hnh ng", response)
      const result = await response.json();
     // console.log("result Action", result)
      setActionMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};
 

  //ly API cho phim hi kch
    const fetchComedyMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchComedyMovies, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
     // console.log("Hi Kch", response)
      const result = await response.json();
     //console.log("result Comedy", result)
      setComedyMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};


    // ly API cho phim kinh d
    const fetchHorrorMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchHorrorMovies, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      const result = await response.json();
      //console.log("Kinh d", response)
      //console.log("resutl Horror", result)
      
      setHorrorMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};
  

    //ly API cho phim lng mn
    const fetchRomanceMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchRomanceMovies, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      //console.log("lng mng", response)
      const result = await response.json();
     
      setRomanceMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};
 

    //ly API cho phim ti liu
    const fetchDocumentaryMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchDocumentaries ,{
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      //console.log("Ti liu", response)
      const result = await response.json();
      //console.log("result Documentary", result)
      setDocumentaryMovies(result.results);
      
    }catch (error) {
      console.log("Error fetching data:", error);
    }};

    // gi truy vn n API t trng input
    const fetchSearchMovies = async () => {
      try {
      const response = await fetch(requestData + requests.fetchSearch ,{
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      console.log("Tm kim ", response)
      const result = await response.json();
      console.log("result Search", result)     
        setSearchTerm(result.results);
    
    }catch (error) {
      console.log("Error fetching data:", error);
    }};


    useEffect(() => {
     
    fetchOriginalMovies();
    fetchTrendingMovies();
    fetchTopRatedMovies();
    fetchActionMovies();
    fetchComedyMovies();
    fetchHorrorMovies();
    fetchRomanceMovies();
    fetchDocumentaryMovies();
     fetchSearchMovies()
    }, []);
 
   
return (
  <div className={styles.app}>
  <Banner/>
{/* a searchTerm lm prop cho Resultlist */}
  <ResultList searchTerm={searchTerm} />

  <div className={styles.container}>   
   <div className={styles.movieList}> 
  <MovieList request={requests.fetchNetflixOriginals} moviePoint={originalMovies} title="Originals"  isOriginals={true}/>
  <MovieList request={requests.fetchTrending} moviePoint={trendingMovies} title="Xu hng"  isOriginals={false} />
  <MovieList request={requests.fetchTopRated} moviePoint={topRatedMovies} title="Xp hng cao"  isOriginals={false}/>
  <MovieList request={requests.fetchActionMovies}  moviePoint={actionMovies} title="Hnh ng"  isOriginals={false}/>
  <MovieList request={requests.fetchComedyMovies} moviePoint={comedyMovies} title="Hi kch"  isOriginals={false}/>
  <MovieList request={requests.fetchHorrorMovies} moviePoint={horrorMovies} title="Kinh d"  isOriginals={false}/>
  <MovieList request={requests.fetchRomanceMovies} moviePoint={romanceMovies} title="Lng mn"  isOriginals={false}/>
  <MovieList request={requests.fetchDocumentaries} moviePoint={documentaryMovies} title="Ti liu"  isOriginals={false}/>
  </div> 
</div>

</div>
  );
}

export default RenderDom;