
import React from "react";
import { useState, useEffect } from "react";
import { requestData } from "./requests";//https://api.themoviedb.org/3
import { token } from "./requests";
import MovieDetail from "../Detail/MovieDetail";// hin th thng tin v video phim
import styles from "./MovieList.module.css";

//Component ny  ly d liu phim t API  render ln web page 
const MovieList = ({ request, title, isOriginals}) => {
  const [movies, setMovies] = useState([]);// cp nht v lu cc b phim ly t API
  const [selectedMovie, setSelectedMovie] = useState(null);// lu tr thng tin ca b phim 
  const fetchData = async () => {
    try {
      //ly d liu API 
      const response = await fetch( requestData  + request, {
        headers: {
          Authorization: `Bearer ${token}`
        }
    });
    // chuyn d liu sang dng JSON
    const result = await response.json();
    //console.log("result", result);
    //console.log(response)
   
      setMovies(result.results);// cp nht trng thi d liu ca trng results vo movies
      
    } catch (error) {
      console.log("Error fetching data:", error);
    }
  }; 
  //console.log(movies)
  useEffect(() => {
    fetchData();
  }, [request]);
  
 
// thc hin render nh dng poster_path cho original 
  const getImagePath = (movie) => {
    // nu isOriginals true , nh dng poster_path s lu vo imagePath cn false s lu  dng backdrop_path vo imagePath
   const imagePath =( isOriginals ? movie.poster_path : movie.backdrop_path) || movie.poster_path;
    return `https://image.tmdb.org/t/p/w500${imagePath}`
  };
//ci ?? ngha l khi no thng (moviePoint === requests.fetchOriginals ? movie.poster_bath:movie.backdropPath) c gi tr l null hoc undefined

const handleClick = (movie) => {
  //console.log("movie", movie);
  setSelectedMovie(movie);// gi hm khi click  cp nht cho selectedMovie 
}

const handleClose = (e) => {
  // e.stopPropagation();//ngn s kin ny truyn n phn t cha 
  setSelectedMovie(null);
};

// hm renderMovies  lp qua movies xc nh hnh nh b phim hin th a vo 1 div 
const renderMovies = () => {
  if (movies.length > 0) {
    return movies.map((movie) => (
      <div 
      key={movie.id} 
      className={styles.movie} 
      onClick={() => handleClick(movie)}>
        {movie.poster_path && (
          <img
            className={isOriginals ? styles.poster_path : styles.backdrop_path}
            src={getImagePath(movie)}
            alt={movie.title}
            style={{ objectFit: "cover" }}
            data-type={isOriginals ? "poster_path" : "backdrop_path"}
          />
        )}
        <h3>{movie.title}</h3>
      </div>
    ));
  }
  return <div>Loading...</div>;
};
return (
  <div>
    <h2>{title}</h2>
    <div className={styles.movieList}>
      {renderMovies()}
    </div>

    {/* &&  kim tra xem selectedMovie c tn ti khng  */}
    {selectedMovie && (
      <>
      {/* nt ng MovieDetail */}
       <div className={styles.close_btn} onClick={handleClose}>
       <span>&#10006;</span>
     </div>
      <MovieDetail
      // dng movieData lm props  MovieDetail x l thng tin phim 
        movieData={selectedMovie}
        // handleClose={handleClose}
         isOriginals={isOriginals}
      />
      </>
    )}
  </div>
);
};

export default MovieList;