import { useState, useEffect } from "react";
import YouTube from "react-youtube";
import { API_KEY } from "../TakeDataAPI/requests";
import "./MovieDetail.css"

// Component ny  hin th thng tin v xem c trailer phim
const MovieDetail = ({ movieData }) => {
const [trailerKey, setTrailerKey] = useState("");// lu key ca trailer c ly v t API
const [currentMovie, setCurrentMovie] = useState(movieData);//cha thng tin ca b phim hin ti c hin th.
const [showDetails, setShowDetails] = useState(false);// hin th thng tin phim
//console.log(movieData)
const movie_id = movieData.id;// ly id ca b phim t movieData  ly video trailer t API 
//console.log(movie_id)
const getTrailer = async () => {
try {
const response = await fetch(
`https://api.themoviedb.org/3/movie/${movie_id}/videos?api_key=${API_KEY}`
);
//console.log(response)
const result = await response.json();
//console.log(result)
const trailers = result.results && result.results.filter((video) => video.site === "YouTube" && video.type === "Trailer");// xc nh site v type hin th 
console.log("trailers", trailers)
if (trailers && trailers.length > 0) {
  setTrailerKey(trailers[0].key);// Nu trailer tn ti ly video trailer u tin
} else if (result.results && result.results.length > 0) {// nu trailer khng tn ti ly video u tin trong results
  setTrailerKey(result.results[0].key);
} else {
  setTrailerKey(null);
}
} catch (error) {
console.log("Error fetching trailer data:", error);
}
};

useEffect(() => {
if (movieData.id !== currentMovie.id) {
setCurrentMovie(movieData);// gi  cp nhp li thng tin  ca b phim
setTrailerKey("");
getTrailer(movieData.id);//gi  tm kim trailer ca b phim hin ti.
}
}, [movieData, currentMovie]);

const vote =( movieData.vote_average);
const { title, overview, release_date,} = currentMovie;

const getImagePath = (movieData) => {
  // nu isOriginals true , nh dng poster_path s lu vo imagePath cn false s lu  dng backdrop_path vo imagePath
 const imgUrl =  movieData.backdrop_path ? movieData.backdrop_path : movieData.poster_path;
  return  `https://image.tmdb.org/t/p/w500${imgUrl}`;
};

return (
<div className="parent_container">
{currentMovie.id && (
<>
{/* false --> true  show thng tin phim */}
<div className="content" onClick={() => setShowDetails(!showDetails)}> 
<h1 className="title">{title}</h1>
<p>Release Date: {release_date}</p>
<p>Vote: {vote}</p>
<p className="content_child">{overview}</p>
</div>
<div className="youtube_container" onClick={() => setShowDetails(!showDetails)}>
{trailerKey && showDetails ? (
<YouTube
className="youtube_video"
videoId={trailerKey}
opts={{
height: "100%",
width: "100%",
playerVars: {
autoplay: 0,
},
}}
/>
) : (
<img src={getImagePath(movieData)} alt={title} />
)}
</div>
</>
)}
</div>
);
};

export default MovieDetail;